import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { LoginService } from './login.service';
import {
  AgentUser,
  LoginUser,
  SupportUserAccount,
} from '../../@core/models/data-objects';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { LoaderService } from '../../modules/shared/services/loader.service';
import { SwalComponent } from '@sweetalert2/ngx-sweetalert2';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnInit {
  showLangMenu = false;
  showLoader = false;
  // need to inhance later
  currentLang = 'en';
  // need to inhance later
  currentFlag = '../../../../assets/images/en.jpg';

  private agentUser: AgentUser;
  loginUser: LoginUser = new LoginUser();

  @ViewChild('username') usernameInput: ElementRef<HTMLInputElement>;
  @ViewChild('password') passwordInput: ElementRef<HTMLInputElement>;
  constructor(
    private loginService: LoginService,
    private router: Router,
    private translate: TranslateService,
    private loader: LoaderService
  ) {}

  ngOnInit(): void {
    this.loader.showLoader$.subscribe((boolen) => {
      this.showLoader = boolen;
    });
  }

  toggleLang() {
    this.showLangMenu = !this.showLangMenu;
  }

  // need to inhance later
  changeLang(e: Event) {
    const elemnt = e.target as HTMLLIElement;
    this.translate.use(elemnt.id).subscribe((data) => {
      this.currentLang = elemnt.id;
      if (this.translate.currentLang === 'ar') {
        document.body.classList.add('rtl');
        this.currentFlag = '../../../../assets/images/ar.jpg';
        localStorage.setItem('lang', elemnt.id);
      } else {
        document.body.classList.remove('rtl');
        this.currentFlag = '../../../../assets/images/en.jpg';
        localStorage.setItem('lang', elemnt.id);
      }
    });
  }

  // LabelVisability(input: HTMLInputElement) {
  //   if (input.value !== '') {
  //     input.previousElementSibling.lastElementChild.classList.add('out');
  //   } else {
  //     input.previousElementSibling.lastElementChild.classList.remove('out');
  //   }
  // }

  submit(e, errorInvalidData: SwalComponent, errorNoData: SwalComponent) {
    if (this.isLoginUserEmptyValues(errorNoData)) return;

    this.loader.showLoader$.next(true);
    localStorage.clear();
    this.loginService.loginUser(this.loginUser).subscribe(
      (data) => {
        this.loader.showLoader$.next(false);
        if (data != null && data.id > 0) {
          this.agentUser = data;
          localStorage.setItem('LoggedUser', JSON.stringify(this.agentUser));
          //this.messageService.invokeSupportConnectionID(this.agentUser.id);
          this.router.navigateByUrl('/dashboard');
        } else errorInvalidData.fire();
      },
      (error) => {
        this.loader.showLoader$.next(false);
        errorInvalidData.fire();
      }
    );

    e.preventDefault();
  }

  isLoginUserEmptyValues(swal: SwalComponent): boolean {
    if (
      this.loginUser.email === undefined ||
      this.loginUser.email == null ||
      this.loginUser.email === '' ||
      this.loginUser.password === undefined ||
      this.loginUser.password == null ||
      this.loginUser.password === ''
    ) {
      // this.usernameInput.nativeElement.value = '';
      // this.passwordInput.nativeElement.value = '';
      this.loginUser.email = '';
      this.loginUser.password = '';
      // this.LabelVisability(this.usernameInput.nativeElement);
      // this.LabelVisability(this.passwordInput.nativeElement);
      swal.fire();
      return true;
    }
    return false;
  }
}
