import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-modal',
  templateUrl: './modal.component.html',
  styleUrls: ['./modal.component.scss'],
})
export class ModalComponent implements OnInit {
  showModal = false;

  constructor() {}

  ngOnInit(): void {}

  closeModal() {
    this.showModal = false;
  }

  stopProbagation(e: MouseEvent) {
    e.stopPropagation();
  }
}
